body {
  font-family: 'Roboto', sans-serif;
  background: #fff;
  /* fallback for old browsers */
}

.header {
  position: relative;
}


.header__line {
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: fff;
  box-shadow: -1px 1px 20px 4px;
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;

}


.navbar {
  padding-bottom: 0;
}

* {
  margin: 0;
  padding: 0;
}



.navbar-logo {
  padding: 15px;
  color: #fff;
  pointer-events: none;
}

.navbar-mainbg {
  background-color: #000000;
  padding: 0px;
}

#navbarSupportedContent {
  overflow: hidden;
  position: relative;
  justify-content: flex-end;
}

#navbarSupportedContent ul {
  padding: 0px;
  margin: 0px;
  margin-right: 35px;
}

#navbarSupportedContent ul li a i {
  margin-right: 10px;
}

#navbarSupportedContent li {
  list-style-type: none;
  float: left;
}

#navbarSupportedContent ul li a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 15px;
  display: block;
  padding: 20px 20px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
}

#navbarSupportedContent>ul>li.active>a {
  color: #000000;
  background-color: transparent;
  transition: all 0.7s;
}

#navbarSupportedContent a:not(:only-child):after {
  content: '\f105';
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 14px;
  font-family: 'Font Awesome 5 Free';
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  transition: 0.5s;
}

#navbarSupportedContent .active>a:not(:only-child):after {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.hori-selector {
  display: inline-block;
  position: absolute;
  height: 100%;
  top: 15px;
  left: 0px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background-color: #fff;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-top: 10px;
}

.hori-selector .right,
.hori-selector .left {
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: #fff;
  bottom: 10px;
}

.hori-selector .right {
  right: -25px;
}

.hori-selector .left {
  left: -25px;
}

.hori-selector .right:before,
.hori-selector .left:before {
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #000000;
}

.hori-selector .right:before {
  bottom: 0;
  right: -25px;
}

.hori-selector .left:before {
  bottom: 0;
  left: -25px;
}

@media (max-width: 991px) {
  .wrapper__nav__btn {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 15px;
    margin: 10px 0 15px 0;
  }

  #navbarSupportedContent ul {
    margin-bottom: 25px;
    margin-right: 0;
    background-color: #000000;

  }

  #navbarSupportedContent ul li a {
    padding: 12px 20px;
    margin: 4px 15px;
  }

  #navbarSupportedContent ul li {
    margin-top: 15px;
  }




  .hori-selector {

    margin-top: 15px;
    margin-left: 10px;
    border-radius: 0;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }

  .hori-selector .left,
  .hori-selector .right {
    right: 10px;
  }

  .hori-selector .left {
    top: -25px;
    left: auto;
  }

  .hori-selector .right {
    bottom: -25px;
  }

  .hori-selector .left:before {
    left: -25px;
    top: -25px;
  }

  .hori-selector .right:before {
    bottom: -25px;
    left: -25px;
  }
}
